@use "global" as *;

////////////////////
// „Éï„Ç©„É≥„Éà„Çµ„Ç§„Ç∫Ë™øÊï¥
////////////////////

html {
  font-size: 16px;

  @media (max-width: $inner) {
    font-size: vw(strip-unit($inner), 16);
  }

  @include mq("md") {
    font-size: 16px;
  }

  //~375px
  @media (max-width: 375px) {
    font-size: vw(375, 16);
  }
}

body {
  font-family: $jp-font;
  color: $black;
  font-feature-settings: "palt";
  letter-spacing: 0.1em;
  line-height: 1.5;
}

.main {
//   background-color: #fff;
}

/* pc„ÅÆÈõªË©±Áï™Âè∑Áô∫‰ø°ÂØæÂøú */
a[href^="tel:"] {
  @include mq("md") {
    pointer-events: none;
  }
}

/* „Éõ„Éê„Éº */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
  @include mq("md") {
    &:hover {
      opacity: 0.8;
    }
  }
}

//pc„ÅßË°®Á§∫
.u-pc {
  display: block;
  @include mq("md") {
    display: none;
  }
}

//„É¢„Éê„Ç§„É´„ÅÆ„ÅøË°®Á§∫
.u-sp {
  display: none;
  @include mq("md") {
    display: block;
  }
}
