@use "global" as *;
.section-btn__link {
  display: inline-flex;
  align-items: center;
  gap: rem(32);
  position: relative;
}

.section-btn__link-text {
  line-height: 2;
  color: $black;
  font-size: rem(16);
  font-weight: $medium;
  transition: color 0.3s ease;
}

.section-btn__link:hover .section-btn__link-text {
  color: $brown;
}

.section-btn__link-arrow {
  width: rem(80);
  height: rem(80);
  border-radius: 50%;
  background-color: $beige;
  border: rem(1) solid $orange;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
  transition: transform 0.3s ease;
  @include mq('md') {
   width: rem(60);
   height: rem(60);
  }
}

.section-btn__link:hover .section-btn__link-arrow {
  transform: translateX(rem(4));
}

.section-btn__link-arrow img {
  width: rem(16);
  height: rem(16);
}

/* üêß „Éö„É≥„ÇÆ„É≥ÔºàÂè≥„Åã„Çâ„Å≤„Çá„Å£„Åì„ÇäÔºâ */
.section-btn__link::after {
  content: "";
  position: absolute;
  right: rem(-10); // „Å©„Çå„Åè„Çâ„ÅÑÂè≥„Åã„ÇâÂá∫„Åô„Åã
  bottom: 0; // È´ò„Åï„ÅÆÂü∫Ê∫ñÔºàË™øÊï¥OKÔºâ
  width: rem(72);
  height: rem(81);
  background: url("../images/common/btn-pen.webp") no-repeat center/contain;
  transform: translateX(-40%); // ÊúÄÂàù„ÅØÂè≥„Å´Èö†„Åó„Å¶„Åä„Åè
  opacity: 0;
  transition: transform 0.3s ease, opacity 0.3s ease;
  pointer-events: none; // „ÇØ„É™„ÉÉ„ÇØ„ÅÆÈÇ™È≠î„Çí„Åó„Å™„ÅÑ
}

/* „Éõ„Éê„ÉºÊôÇÔºö„Éö„É≥„ÇÆ„É≥„Åå„Çπ„É©„Ç§„Éâ„Ç§„É≥ */
.section-btn__link:hover::after {
  transform: translateX(30%);
  opacity: 1;
}
